.wrapper .button {
  display: inline-block;
  height: 45px;
  width: 45px;
  margin: 0 5px;
  overflow: hidden;
  background: transparent;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-out;
}

.wrapper .button:hover {
  width: 200px;
}

.wrapper .button .icon {
  display: inline-block;
  height: 35px;
  width: 35px;
  text-align: center;
  border-radius: 10px;
  box-sizing: border-box;
  line-height: 60px;
  transition: all 0.3s ease-out;
}

.wrapper .button .icon i {
  font-size: 25px;
  line-height: 60px;
  transition: all 0.3s ease-out;
}

.wrapper .button:hover .icon i {
  color: rgb(255, 0, 0);
}

.wrapper .button span {
  font-size: 20px;
  font-weight: 500;
  line-height: 60px;
  margin-left: 10px;
  transition: all 0.3s ease-out;
}

.wrapper .button:nth-child(1) span {
  color: #4267b2;
  align-items: center;
}

.wrapper .button:nth-child(2) span {
  color: #1da1f2;
  align-items: center;
}

.wrapper .button:nth-child(3) span {
  color: #e1306c;
  align-items: center;
}

.wrapper .button:nth-child(4) span {
  color: #ff0000;
  align-items: center;
}

