@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
  font-family: 'Poppins', sans-serif;
}
.about {
  display: flex;
  justify-content: center;
  align-items: center;

  transition: 1s background;
  background: linear-gradient(
    332deg,
    rgb(255, 115, 0) 0%,
    rgb(145, 68, 128) 100%
  );

  animation: kenan 10s 2s reverse;
}
@keyframes kenan {
  0% {
    background: lightcoral;
  }
  25% {
    background: #5c4f74;
  }
  50% {
    background: #c50d66;
  }
  75% {
    background: #525564;
  }
  100% {
    background: linear-gradient(
      332deg,
      rgb(113, 53, 133) rgb(255, 115, 0) 0%,
      rgb(145, 68, 128) 100%
    );
  }
}

