.about1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 90vh;
}
.about2 {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 90vh;
  flex-direction: column;
}
.about2 h1 {
  font-size: 2rem;
  font-weight: bold;
}

.about2 p {
  font-size: 1.2rem;
}
.mission {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  background: url("../assets/bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.content p {
  line-height: 2;
  color: black;

  font-size: 15px;
}
.logo {
  animation: logo 10s alternate 2s infinite;
}
@keyframes logo {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.team {
  height: 65vh;
  background: lightgrey;
}
